@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
.NavMain {
  position: -webkit-sticky;
  position: sticky;
  z-index: 2;
  background-color: transparent;
  left: 0;
  right: 0;
  top: 0;
  color: rgb(57, 57, 57);
  display: flex;
  align-items: center;
  padding: 20px 40px;
}

.NavMain .logo {
  font-size: 2.2rem;
  font-weight: 600;
  margin: 1.2rem;
  text-shadow: 2px 2px 2px grey;
  z-index: 1;
}

.NavMain ul {
  display: flex;
  list-style-type: none;
  margin-left: auto;
  font-weight: 600;
  font-size: 1.2rem;
}

.NavMain li {
  cursor: pointer;
}

.NavMain ul li:not(:last-child) {
  margin-right: 1.5em;
}

.Item-container {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  left: 25px;
  top: 0px;
  box-shadow: 0 0 50px lightgrey;
  border-radius: 20px;
  background-color: white;
  width: 450px;
  padding: 25px;
  margin: 75px auto;
  z-index: 100;
  overflow-y: auto;
  max-height: 75vh;
}

.Item-container .round-image {
  height: 150px;
  width: 150px;
  border-radius: 100%;
  overflow: hidden;
  position: relative;
  margin: 25px auto 10px;
}

.Item-container .round-image .user-img {
  width: 100%;
  object-position: 0 0;
  height: 100%;
  object-fit: cover;
}

.Item-container .round-image-user {
  min-height: 30px;
  min-width: 30px;
  border-radius: 100%;
  overflow: hidden;
  position: relative;
  margin-right: 10px;
}

.Item-container .round-image-user img {
  position: absolute;
  width: 100%;
  object-fit: cover;
}

.Item-container .info {
  margin: 10px auto;
}

.Item-container .info span {
  display: inline;
  margin: 5px 5px;
}

.Item-container .user-info {
  margin-top: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Item-container .contact-information {
  text-align: center;
  margin-top: 20px;
  background: #f1f2ff;
  padding: 15px;
  border-radius: 10px;
  color: #5663ff;
  font-size: 14px;
  font-weight: 500;
}

.Item-container .title {
  margin: 0;
  text-align: center;
  text-transform: capitalize;
}

.Item-container .info .description,
.location {
  margin: 1em 0;
}

.Item-container .description {
  background-color: rgb(226, 226, 226);
  padding: 10px;
  border-radius: 10px;
}

.AutocompletePlace {
  position: relative;
}

.AutocompletePlace-results {
  position: absolute;
  background-color: white;
  padding: 0;
  margin: 0;
  border: 1px solid #00000011;
}

.AutocompletePlace-items {
  list-style: none;
  border: 1px solid #00000011;
  cursor: pointer;
  padding: 10px 10px;
}

.AutocompletePlace-items:hover {
  background-color: #0000ff11;
}

.UploadWidget {
  border: 1px solid #5663ff;
  color: #5663ff;
  border-radius: 4px;
  display: inline-block;
  padding: 10px 22px;
  cursor: pointer;
  margin-bottom: 20px;
}

.UploadWidget.input[type="file"] {
  display: none !important;
}

.Btn {
  background: #5663ff;
  width: 100%;
  color: white;
  padding: 16px 0;
  font-size: 16px;
  border-radius: 10px;
  border: none;
  outline: none;
  cursor: pointer;
}

.Btn:active {
  transform: translate(1px, 1px);
}

.Btn.btn-primary {
  background: #5663ff;
  color: white;
}

.Btn.btn-primary:disabled {
  background: rgb(190, 190, 190);
}

.Btn.btn-secondary {
  background: white;
  color: #5663ff;
  border: 1px solid #5663ff;
}

.Message {
  display: flex;
  flex-direction: row;
  padding: 15px;
  width: 100%;
  border-radius: 10px;
  font-size: 14px;
  font-weight: 500;
  margin: 10px 0;
}

.Message .icon {
  margin-right: 8px;
}

.Message.info {
  color: #5663ff;
  background-color: #f1f2ff;
}

.Message.info .icon {
  fill: #5663ff;
}

.Message.danger {
  color: rgb(255, 0, 0);
  background-color: rgb(255, 215, 215);
}

.Message.danger .icon {
  fill: rgb(255, 0, 0);
}

.ItemForm-container {
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  right: 25px;
  top: 0px;
  z-index: 40;
  box-shadow: 0 0 50px lightgrey;
  border-radius: 20px;
  background-color: white;
  width: 400px;
  margin: 75px auto;
  z-index: 100;
  overflow-y: auto;
  max-height: 75vh;
}

.ItemForm {
  background: white;
  padding: 30px;
  border-radius: 20px;
  box-shadow: rgba(16, 35, 82, 0.02) 0 7px 50px 20px;
}

.ItemForm .close-link {
  text-align: right;
  font-weight: 900;
  font-size: 1.4rem;
  cursor: pointer;
  float: right;
  text-decoration: none;
  color: black;
}

.ItemForm .input {
  margin: 8px 0 12px;
  width: 100%;
  border-radius: 4px;
  display: block;
  border: none;
  font-size: 14px;
  background: #eff1f6;
  padding: 12px 12px;
}

.ItemForm select {
  display: block;
  font-size: 14px;
  line-height: 1.3;
  padding: 12px 12px;
  width: 100%;
  box-sizing: border-box;
  margin: 10px 0;
  border-radius: 4px;
  border: 0;
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  background: #eff1f6;
  background-image: url("/assets/arrow.svg");
  background-repeat: no-repeat, repeat;
  background-position: right 18px top 50%, 0 0;
  background-size: 0.65em auto, 100%;
}

.message {
  display: flex;
  flex-direction: row;
  background-color: #f1f2ff;
  padding: 15px;
  width: 94%;
  border-radius: 10px;
  color: #5663ff;
  font-size: 14px;
  font-weight: 500;
}

.ItemForm .message img {
  margin-right: 8px;
}

.FeedBack {
  width: 100%;
  padding: 10px 20px;
  border-radius: 10px;
  font-size: 18px;
  text-align: center;
}

.FeedBack.failure {
  background-color: rgb(255, 182, 182);
  color: rgb(255, 58, 58);
}

.FeedBack.success {
  background-color: rgb(61, 194, 0);
  color: white;
}

.CardItems {
  text-align: center;
  margin-top: 20px;
}

.CardItem {
  min-width: 500px;
  margin-top: 50px;
  display: flex;
  padding: 20px 40px;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: space-between;
  background: white;
  max-width: 800px;
  margin-bottom: 20px;
  border-radius: 10px;
}

.CardItem .round-image {
  height: 100px;
  width: 100px;
  margin-left: 10px;
  /* margin: 0 auto; */
  border-radius: 100%;
  overflow: hidden;
  position: relative;
}

.CardItem .item-empty {
  margin-top: 60px;
  display: flex;
  padding: 40px 40px;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  max-width: 800px;
  margin-bottom: 20px;
  border-radius: 10px;
}

.CardItem .item-empty button {
  margin-top: 20px;
  width: 100%;
  background: #5663ff;
  color: white;
  font-size: 16px;
  padding: 12px 16px;
  border-radius: 10px;
  border: none;
}

.CardItem .round-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.CardItem .description {
  flex: 2 1;
  text-align: center;
}

.CardItem .description h2 {
  margin: 10px 0;
}
.CardItem .description h2 {
  margin: 5px 0;
  font-weight: 400;
}

.CardItem .buttons {
  display: flex;
}

.CardItem > h3 {
  text-align: center;
}

.Profile {
  max-width: 800px;
  margin: 10vh auto 0;
  padding: 0 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: auto;
}

.Profile .user-contact {
  margin-top: 20px;
  background: #f1f2ff;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px;
  border-radius: 10px;
}

.Profile .title {
  margin: 10px 0;
}

.Profile .stack.empty {
  margin-top: 60px;
  display: flex;
  padding: 40px 40px;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  max-width: 800px;
  margin-bottom: 20px;
  border-radius: 10px;
}

.Profile .stack.empty button {
  margin-top: 20px;
  width: 100%;
  background: #5663ff;
  color: white;
  font-size: 16px;
  padding: 12px 16px;
  border-radius: 10px;
  border: none;
}

.Profile .user-image {
  margin: 0px auto;
  height: 200px;
  width: 200px;
}

.Profile .user-image.round-image {
  height: 250px;
  width: 250px;
  border-radius: 100%;
  overflow: hidden;
  position: relative;
}

.Profile .user-presentation {
  text-align: center;
}

.Profile .user.contact {
  margin-top: 20px;
  background: #f1f2ff;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px;
  border-radius: 10px;
}

.Profile .user.contact h4 {
  text-align: center;
  margin-top: 0;
}

.Profile .form-group .input {
  margin-top: 8px;
  width: 350px;
  border-radius: 4px;
  display: block;
  border: 1px solid #5663ff;
  font-size: 16px;
  background: transparent;
  padding: 12px 12px;
}

.Profile .form-group .label {
  font-size: 14px;
  font-weight: 600;
}

/* $primary: #5663ff;
$lightGray: #eff1f6; */

.form-section {
  background: white;
  max-width: 550px;
  margin: 3vh auto 0;
  padding: 0 20px;
  max-height: 75vh;
}

.form-section .round-image {
  height: 250px;
  width: 250px;
  border-radius: 100%;
  overflow: hidden;
  margin: 0 auto 20px;
}

.form-section .round-image img {
  object-fit: cover;
  object-position: center;
}

.form-section .header {
  text-align: center;
}

.form {
  background: white;
  padding: 30px;
  border-radius: 20px;
  box-shadow: rgba(16, 35, 82, 0.02) 0 7px 50px 20px;
}

.form .title {
  margin: 10px 0;
}

.form-group .input {
  margin: 8px 0 12px;
  width: 100%;
  border-radius: 4px;
  display: block;
  border: none;
  font-size: 14px;
  background: #eff1f6;
  padding: 12px 12px;
}

.form-group .input-error {
  padding: 8px 16px;
  border-radius: 4px;
  background-color: rgb(255, 192, 192);
  color: rgb(255, 34, 34);
}

.form-group .text-area {
  display: block;
  font-size: 14px;
  width: 100%;
  background: #eff1f6;
  border: none;
  border-radius: 4px;
  line-height: 1.3;
  resize: none;
  box-sizing: border-box;
  padding: 12px;
  margin-top: 10px;
  margin-bottom: 10px;
  font-family: "Manrope", sans-serif;
}

.form-group .label {
  font-weight: 600;
}

.form button {
  margin-top: 30px;
  background: #5663ff;
  width: 100%;
  color: white;
  padding: 16px 0;
  font-size: 16px;
  border-radius: 10px;
  border: none;
}

.form-section.link {
  text-align: center;
}

*,*::before,*::after{box-sizing:border-box}ul[class],ol[class]{padding:0}body,h1,h2,h3,h4,p,ul[class],ol[class],figure,blockquote,dl,dd{margin:0}html{scroll-behavior:smooth}body{min-height:100vh;text-rendering:optimizeSpeed;line-height:1.5}ul[class],ol[class]{list-style:none}a:not([class]){-webkit-text-decoration-skip:ink;text-decoration-skip-ink:auto}img{max-width:100%;display:block}article>*+*{margin-top:1em}input,button,textarea,select{font:inherit}img:not([alt]){-webkit-filter:blur(10px);filter:blur(10px)}@media(prefers-reduced-motion:reduce){*{-webkit-animation-duration:.01ms !important;animation-duration:.01ms !important;-webkit-animation-iteration-count:1 !important;animation-iteration-count:1 !important;transition-duration:.01ms !important;scroll-behavior:auto !important}}
* {
  margin: 0;
  padding: 0;
}

html {
  font-family: "Manrope", sans-serif;
}

a {
  text-decoration: none;
  color: inherit;
}

body {
  background: #f8f9fd;
}

h2 {
  font-size: 1.1rem;
}

.page {
  padding-top: 80px;
}

.link {
  color: #5663ff;
  font-weight: 700;
}

